// include gauge chart
require('echarts/lib/chart/gauge');

// include tooltip component
require('echarts/lib/component/tooltip');
// include title component
require('echarts/lib/component/title');
// include legend component
require('echarts/lib/component/legend');


export default class GaugechartController {
	constructor() {
		"ngInject";
		this.initGaugeChart();
	}

	initGaugeChart() {
		this.gaugeConfig = {
			theme: 'default',
			dataLoaded: true
		};

		this.gaugeOption = {
			series: [{
				name: "",
				z: 3,
				type: "gauge",
				startAngle: 180, //360180
				endAngle: 0,
				center: ["50%", "77%"], //
				radius: 80,
				axisLine: {
					lineStyle: {
						width: 3, //
						color: [[1, "#1fbba6"], [1, "#dbdbdb"]] //0.298
					}
				},
				axisTick: {
					show: false
				},
				axisLabel: {
					show: false
				},
				splitLine: {
					show: false
				},
				pointer: {
					width: 0, //
					length: "80%", //
					color: "#2d99e2"
				},
				title: {
					show: false
				},
				detail: {
					show: false
				},
				data: [{ //
					value: 100,
					name: "100%"
				}]
			},
				{
					name: "",
					type: "gauge",
					startAngle: 180, //360180
					endAngle: 0,
					center: ["50%", "77%"], //
					radius: 75,
					axisLine: {
						lineStyle: {
							width: 30, //
							color: [[0.305, "#1fbba6"], [1, "#dbdbdb"]] //0.298
						}
					},
					axisTick: {
						show: false
					},
					axisLabel: {
						show: false
					},
					splitLine: {
						show: false
					},
					pointer: {
						width: 0, //
						length: "80%", //
						color: "#2d99e2"
					},
					title: {
						show: true,
						offsetCenter: [0, '-10px'], //
						textStyle: { //
							color: "#1fbba6",
							fontSize: 18,
							fontFamily: "Arial",
							fontWeight: "normal"
						}
					},
					detail: {
						show: false
					},
					data: [{ //
						value: 30.50,
						name: "30.50%"
					}]
				}]
		};
	}
}


